html {
    box-sizing: border-box;
    scroll-behavior: smooth;
}

* {
    box-sizing: border-box;
    &::before {
        box-sizing: border-box;
    }
    &::after {
        box-sizing: border-box;
    }
}

.container {
    width: 1280px;
    margin-inline: auto;
    padding-inline: 40px;
}